import helper from '../../common/helper.js';
var _self;
export default {
	data() {
		lists: [],
		self = this
		return {
			mobile: '',
			password: '',
			openids: '',
			openid: '',
			appid: 'wxf3faac5f46f05b2e',
			secret: '31dab934c364c3ce9f1a355ccd9cfe1f'
		};
	},
	onLoad: function() {
		_self = this;
		if (helper.islogin()) {
			uni.switchTab({
				url: '../index/index'
			})
		}
	},
	methods: {
		openregister() { //打开注册页面
			uni.navigateTo({
				url: 'register',
			});
		},
		openphonelogin() { //打开手机短信短信登录页面 
			uni.navigateTo({
				url: 'phonelogin',
			});
		},
		MerchabtBut() { //打开商家登录页面
			uni.navigateTo({
				url: 'MerLogin'
			});
		},
		loginbtn() { //手机登录按钮
			if (this.mobile == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入登录手机号',
					duration: 1500,
				});
				return;
			}
			if (!helper.checkmobile(this.mobile)) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的手机号码',
					duration: 1500,
				});
				return;
			}
			if (this.password.length < 6 || this.password.length > 12) {
				uni.showToast({
					icon: 'none',
					title: '请输入6-12个字符的密码',
					duration: 1500,
				});
				return;
			}
			uni.showLoading({
				mask: true,
				title: '登录中，请稍候'
			});
			uni.request({
				url: helper.websiteUrl + '/user/loginbymobile',
				data: {
					loginMark: helper.getloginMark(),
					token: '',
					data: '{"mobile": "' + self.mobile + '","password": "' + self.password + '","type":"1"}'
				},
				method: 'GET',
				dataType: 'json',
				success: (res) => {
					uni.hideLoading();
					if (res.data.code == 200) {
						//console.log(JSON.stringify(res))
						var userinfo = res.data.data.baseinfo;
						uni.setStorageSync("token", userinfo.token);
						helper.setstate(userinfo.userId, userinfo.mobile, userinfo.nickName);
						uni.showToast({
							title: '登录成功'

						});
						uni.reLaunch({
							url: '../index/index'
						});
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				}
			});
		},
