			});
			//获取微信会员信息
			uni.login({
				provider: 'weixin',
				success: function(res) {
					var objz = {};
					if (res.code || res.authResult.openid) {
						uni.getUserInfo({
							success: function(resuserinfo) {
								//APP登陆













































								//小程序登陆

