									},
									method: 'GET',
									dataType: 'json',
									success: function(ress) {
										if (ress.data.code == 200) {
											var openid = ress.data.data.openinfo.openid;
											//console.log(openid);
											//注册和登录
											uni.request({
												url: helper.websiteUrl + 'user/reg',
												data: {
													loginMark: helper.getloginMark(),
													token: '',
													data: '{"openid": "' + openid + '","headimgurl":"' + resuserinfo.userInfo.avatarUrl +
														'","nikename":"' +
														resuserinfo.userInfo.nickName +
														'","province":"' + resuserinfo.userInfo.province + '","country":"' + resuserinfo.userInfo.country +
														'","city":"' + resuserinfo.userInfo.city +
														'","sex":"' + resuserinfo.userInfo.gender + '"}'
												},
												method: 'GET',
												dataType: 'json',
												success: (data) => {
													uni.hideLoading();
													if (data.data.code == 200) {
														var userinfo = data.data.data.baseinfo;
														console.log(userinfo)
														uni.setStorageSync("token", userinfo.token);
														helper.setstate(userinfo.userId, userinfo.mobile, userinfo.nickName);
														uni.showToast({
															title: '登录成功'
														});
														uni.switchTab({
															url: '../index/index'
														});
													} else {
														uni.showToast({
															icon: 'none',
															title: data.data.info,
															duration: 1500,
														});
													}
												},
												fail: () => {
													uni.hideLoading();
													uni.showToast({
														icon: 'none',
														title: '网络异常,请稍后重试'
													});
												}
											});
										} else {
											uni.hideLoading();
											uni.showToast({
												icon: 'none',
												title: '获取会员信息失败！'
											});
										}
									},
									fail: function() {
										uni.hideLoading();
										uni.showToast({
											icon: 'none',
											title: '获取信息出错！'
										});
									}

								});

							},
							fail: function() {
								uni.hideLoading();
								uni.showToast({
									icon: 'none',
									title: '获取用户信息失败！'
								});
							}
						});
					} else {
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title: '获取用户登录状态失败！' + JSON.stringify(res)
						});
					}
				},
				fail: function() {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络错误，请稍后再试'
					});
				}
			});
		},
		openpage() {
			uni.navigateTo({
				url: '../my/resetpassword',
			});
		}
	}
}
